      .horizontal-ticker {
        display: flex;
        align-items: center;
        height: 6em;
        text-decoration: none;
        white-space: nowrap;
        color:var(--pale-blue);
        background-color: var(--purple);
      }
      .horizontal-ticker:hover {
        background: var(--neon-green);
        color: var(--vermilion);
      }

      .horizontal-ticker svg {
        fill: aquamarine;
      }
      .horizontal-ticker:hover svg {
        fill:white;
      }
      .horizontal-ticker-title {
        list-style: none;
        line-height: 1;
        font-weight: 400;
        margin: 0;
        position: absolute;
        display: flex;
      }
    
      #home-programme-mirage-creative-title {
        -webkit-animation: backward-infinite 10s linear infinite;
        animation: backward-infinite 10s linear infinite;
      }
      .has-intersection .horizontal-ticker-title {
        animation-play-state: paused !important;
      }
      .has-intersection .is-in-view .horizontal-ticker-title {
        animation-play-state: running !important;
        will-change: transform;
      }
      .horizontal-ticker-link:hover .horizontal-ticker-title,
      .has-intersection
        .is-in-view
        .horizontal-ticker-link:hover
        .horizontal-ticker-title {
        animation-play-state: paused !important;
      }
  
    @media screen and (orientation: portrait) {
      .horizontal-ticker {
        font-size: 12.5vw;
      }
    }
    @media print {
      h4 {
        font-family: var(--font-family-cheee-jimbo);
      }
      h4 {
        font-size: 12pt;
      }
      a {
        text-decoration: none;
