  /*! CSS Used keyframes */
  @keyframes backward-infinite {
    0% {
      transform: translate3D(0, 0, 0);
    }
    100% {
      transform: translate3D(calc((-100% / 3) - 0.125em), 0, 0);
    }
  }
  
