}

.button-pill {
  border: 2px solid var(--vermilion);
}

.button-pill-light-blue {
  color: var(--pale-blue);
  border: 2px solid var(--pale-blue);
  background-color: #0000;
}

.button-pill-red {
  background-color: var(--vermilion);
  color: var(--pale-blue);
}

.button-pill-green {
